import { time } from 'console'
import React, { ChangeEvent, useState } from 'react'
import Message from './Message'
import s from './Message.module.css'

/* const messageData = {
    avatar: 'https://sun9-74.userapi.com/Ph-WiuOtF985il9AvN9JqiCWedmHtSGSSTXrSA/ltEB2Z2-YO4.jpg',
    name: 'Some Name',
    message: 'some text',
    time: '22:00',
} */
type MessageDataType = {
    avatar: string
    name: string
    message: string
    time: string
}

function HW1() {

    let [newMessage, setNewMessage] = useState('')

    let [messageData, setMessageData] = useState<Array<MessageDataType>>([
        {
            avatar: 'data:image/jpeg;base64,',
            name: 'Some Name',
            message: 'some text',
            time: '22:00',
        },
    ]
    )

    const addNewMessage = (newMessage: string) => {
        let newMessageObj: MessageDataType = {
            avatar: 'data:image/jpeg;base64,',
            name: 'Nick',
            message: newMessage,
            time: '10:10'
        }
        setMessageData([...messageData, newMessageObj])
    }

    const onChangeHandler = (e: ChangeEvent<HTMLTextAreaElement>) => {
        setNewMessage(e.currentTarget.value)
    }
    const onClickHandler = () => {
        if (newMessage !== '') {
            addNewMessage(newMessage)
            setNewMessage('')
        }
    }
    return (
        <div>
            <hr />
            homeworks 1

            {/*should work (должно работать)*/}

            {
                messageData.map(md => <Message

                    avatar={md.avatar}
                    name={md.name}
                    message={md.message}
                    time={md.time}
                />
                )
            }
            <div className={s.remoteBlock}>
                <textarea value={newMessage} onChange={onChangeHandler} />
                <button onClick={onClickHandler} className={s.addMessageButton}>Add Message</button>
            </div>


            <hr />
            {/*для личного творчества, могу проверить*/}
            {/*<AlternativeMessage/>*/}
            <hr />
        </div>
    )
}

export default HW1
